.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: var(--almost-black);
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--blue);
}

.is-active {
  color: var(--blue);
  position: relative;
}

.group {
  justify-content: space-between;
  display: flex;
}

.group__reviews {
  text-align: right;
  align-items: center;
  height: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.group--top {
  margin-top: 16px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  margin-right: 4px;
}

.stars__star--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  border: 1px solid var(--light-gray);
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__name {
  color: var(--almost-black);
  margin: 40px auto 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: var(--gray);
  margin: 4px 0 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  height: 18px;
  margin-top: 24px;
}

.card__label {
  text-align: left;
  color: var(--gray);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__value {
  text-align: right;
  color: var(--almost-black);
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: var(--dark-blue);
}

.card__button {
  background: var(--blue);
  color: var(--white);
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  left: 17px;
}

.card__button:hover {
  border: 1px solid var(--blue);
  color: var(--blue);
  background-color: #fff;
  border-radius: 5px;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
  text-align: left;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

* {
  box-sizing: border-box;
}

:root {
  --blue: #00acdc;
  --white: #fff;
  --dark-blue: #34568b;
  --light-gray: #f3f3f3;
  --almost-black: #060b35;
  --gray: #616070;
}
